<template>
  <div class="page-container">
    <div v-if="!isUpdateMode" class="page-header">
      <h1>ü§ñ AI Agents Launcher</h1>
      <p class="subtitle">Create your own AI Agent for the BITCAT Universe or for your own Telegram group</p>
    </div>
    <div v-else class="page-header">
      <h1>ü§ñ Update AI Agent</h1>
      <p class="subtitle">Upgrade your AI agent and make it better</p>
    </div>
    <!-- Agent Carousel -->
    <div class="agent-carousel" v-if="!isUpdateMode">
      <span class="carousel-title">Sample AI Agents</span>
      <transition name="fade" mode="out-in">
        <div class="carousel-slide" :key="currentSlide">
          <AiAgentCard v-bind="sampleAgents[currentSlide]" @more-info="handleMoreInfo(sampleAgents[currentSlide])" />
        </div>
      </transition>
      <div class="carousel-indicators">
        <button 
          v-for="(_, index) in sampleAgents" 
