          :class="{ active: currentSlide === index }"
          @click="setSlide(index)"
        ></button>
      </div>
    </div>
    
    <div class="create-agent-page">

      <div class="create-form">
        <!-- Basic Information -->
        <div class="form-section">
          <h2>Basic Information</h2>
          <h2 class="warning-message" v-if="!store.getters.isAuthenticated">Your need to be logged in to create an AI Agent. <router-link to="/my" class="more-info">Login ‚Üí</router-link></h2>

          <div class="avatar-preview" @click="showAvatarPopup = true">
            <img :src="prepareAvatarUrl(selectedAvatar)" alt="Agent Avatar">
            <div class="avatar-overlay">Change Avatar</div>
          </div>

          <div class="form-group">
            <label>Agent Name<span class="char-counter" :class="{ 'near-limit': agentData.name.length > 40 }">({{ agentData.name.length }}/50)</span></label>
            <input 
              v-model="agentData.name" 
              type="text" 
              placeholder="Enter agent name"
