  padding: 10px 0;
  /* max-width: 1200px; */
  margin: 0 auto;
  background: transparent;
  background: linear-gradient(to bottom, #e38f00 0%, #f8f9fa 400px);
}

.agent-carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 100%;
  margin-bottom: 40px;
  border-radius: 20px;
  padding: 20px 0;
}

.carousel-title {
  font-size: 1.1rem;
  margin-top: 20px;
  color: #555;
}

.carousel-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px 20px;
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #ddd;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background: #e38f00;
  transform: scale(1.2);
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
